import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {ConfigProvider} from "../../providers/config/config";



@IonicPage()
@Component({
  selector: 'page-i-transfer-dest-account',
  templateUrl: 'i-transfer-dest-account.html',
})
export class ITransferDestAccountPage {
  accountNo;
  transferDetails01;
  destAccList;
  searchTerm;
  formInputValue;
  filterData = [];

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public config: ConfigProvider) {
    this.accountNo = this.navParams.get("accountNo");
    this.transferDetails01 = JSON.parse(this.navParams.get("transferDetails01"));
    this.destAccList = this.transferDetails01.destAccList;
    this.formInputValue = this.navParams.get("formInputValue");

    if (this.destAccList == null) {
      this.destAccList = [];
    }
    this.filterData = this.destAccList;
  }

  setFilteredItems = function () {
    this.filterData = this.destAccList.filter((dest) => {
      return ((dest.subscriptionValue.toLowerCase().indexOf(this.searchTerm.toLowerCase()) > -1) ||
        (dest.subscriptionDesc.toLowerCase().indexOf(this.searchTerm.toLowerCase()) > -1));
    });
  };

  selectDestAccount = function (selectedAccount) {
    this.navCtrl.pop({animate: false});
    this.navCtrl.push('ITransfer02Page', {
      'accountNo': this.accountNo,
      'transferDetails01': JSON.stringify(this.transferDetails01),
      'selectedAccount': JSON.stringify(selectedAccount),
      'sourcePage': JSON.stringify("destList"),
      'formInputValue' : this.formInputValue
    });
  };

  ionViewDidLoad() {
  }

}



